.PanelHeader__in {
    background-color: #567599 !important;
    background: -webkit-linear-gradient(90deg, #6f1684,#5b0b98,#6a0ab3);
    background: linear-gradient(90deg, #6f1684,#5b0b98,#6a0ab3);
    border-bottom: 1px solid #404040;
  }

  .Panel__in {
    background: rgba(0, 0, 0, 0)!important;
  }

  .Panel::after {
    position: fixed!important;
    background-color: #567599!important;
    background: url(/src/img/fon.jpeg);
    background: -webkit-linear-gradient(90deg, #6f1684,#5b0b98,#6a0ab3);
    background: linear-gradient(90deg, #6f1684,#5b0b98,#6a0ab3);
    background-position: center;
    background-size: auto 100%;
    background-position: center!important;
    background-repeat: no-repeat!important;
    filter: blur(3px);
    filter: brightness(60%);
  }

.vkuiFormField__border {
  background-color: #1e0b63 !important;
  background: -webkit-linear-gradient(90deg, #6f1684,#5b0b98,#6a0ab3);
    background: linear-gradient(90deg, #6f1684,#5b0b98,#6a0ab3);
  opacity: 0.9;
  }

  .vkuiCustomSelect__options {
    background-color: #1e0b63 !important;
    background: -webkit-linear-gradient(90deg, #6f1684,#5b0b98,#6a0ab3);
    background: linear-gradient(90deg, #6f1684,#5b0b98,#6a0ab3);
    opacity: 0.9;
  }

