.result-card {
    border-radius: 10px;
    box-shadow: 7px 7px 5px rgba(0,0,0,0.6); 
    text-shadow: 7px 7px 5px rgba(0,0,0,0.6); 
    font-weight: 700;

    margin: 5px;

    background: url(/src/img/life.png);
    background-position: center;
    background-size: auto 150%;
    max-width: 630px;
  }

  .result-card-fon {
    display: grid;
    background-color: #450e528d;
    border-radius: 10px;
    padding: 5px;
    text-align: center;
    font-weight: 700;
    min-height: 200px;
    align-items: center;
    justify-content: center;
}

